const state = () => ({
  total: 0,
  list: [],
  delivery: 0,
  discount: 0,
  subtotal: 0,
  promoCode: '',
  discountAmount: 0,
});

const mutations = {
  addToCart(state, product) {
    const inCart = state.list.find((item) => item.id === product.id);

    if (inCart) {
      state.list = state.list.map((item) => {
        if (item.id === product.id) {
          if (item.quantity) {
            item.quantity += 1;
          }
        }
        return item;
      });
      state.subtotal += Number(product.price);
      state.discountAmount = Number((state.subtotal - state.total).toFixed(2));
      state.total += Number(product.price) * (1 - state.discount / 100);
    } else {
      state.list.push({
        ...product,
        quantity: 1,
      });
      state.subtotal += Number(product.price);
      state.total += Number(product.price) * (1 - state.discount / 100);
    }
  },
  removeFromCart(state, product) {
    const inCart = state.list.find((item) => item.id === product.id);

    if (inCart) {
      state.list = state.list.map((item) => {
        if (item.id === product.id && item.quantity > 1) {
          item.quantity -= 1;
        } else if (item.id === product.id && item.quantity === 1) {
          state.list.splice(state.list.indexOf(item), 1);
        }
        return item;
      });
      state.subtotal -= Number(product.price);
      state.discountAmount = Number((state.subtotal - state.total).toFixed(2));
      state.total -= Number(product.price) * (1 - state.discount / 100);

      if (state.list.length === 0) {
        state.discount = 0;
        state.promoCode = '';
      }
    }
  },
  setDiscount(state, discount) {
    if (state.list.length === 0) {
      state.discount = 0;
    } else {
      state.discount = discount;
    }
    const newTotal = state.subtotal * (1 - state.discount / 100);
    state.discountAmount = Number((state.subtotal - newTotal).toFixed(2));
    state.total = state.subtotal * (1 - state.discount / 100);
  },
  resetCart(state) {
    state.list = [];
    state.subtotal = 0;
    state.total = 0;
    state.discount = 0;
    state.promoCode = '';
    state.delivery = 0;
    state.discountAmount = 0;
  },
  setPromoCode(state, promoCode) {
    state.promoCode = promoCode;
  },
};

const actions = {
  addToCart({commit}, product) {
    commit('addToCart', product);
  },
  removeFromCart({commit}, product) {
    commit('removeFromCart', product);
  },
  setDiscount({commit}, discount) {
    commit('setDiscount', discount);
  },
  resetCart({commit}) {
    commit('resetCart');
  },
  setPromoCode({commit}, promoCode) {
    commit('setPromoCode', promoCode);
  },
};

const getters = {
  items(state) {
    return state.list;
  },
  total(state) {
    return state.total;
  },
  subtotal(state) {
    return state.subtotal;
  },
  discount(state) {
    return state.discount;
  },
  promoCode(state) {
    return state.promoCode;
  },
  discountAmount(state) {
    return state.discountAmount;
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
