// Начальное состояние
const state = {
  list: [],
};

// Геттеры
const getters = {
  wishlist: (state) => state.list,
};

// Мутации
const mutations = {
  ADD_TO_WISHLIST(state, product) {
    const inWishlist = state.list.find((item) => item.id === product.id);

    if (!inWishlist) {
      state.list.push(product);
    }
  },
  REMOVE_FROM_WISHLIST(state, product) {
    state.list = state.list.filter((item) => item.id !== product.id);
  },
  SET_WISHLIST(state, products) {
    state.list = products;
  },
  UPDATE_LIST(state, products) {
    state.list = products;
  },
  RESET_WISHLIST(state) {
    state.list = [];
  },
};

// Действия
const actions = {
  addToWishlist({commit}, product) {
    commit('ADD_TO_WISHLIST', product);
  },
  removeFromWishlist({commit}, product) {
    commit('REMOVE_FROM_WISHLIST', product);
  },
  setWishlist({commit}, products) {
    commit('SET_WISHLIST', products);
  },
  updateList({commit}, products) {
    commit('UPDATE_LIST', products);
  },
  resetWishlist({commit}) {
    commit('RESET_WISHLIST');
  },
};

// Экспорт модуля
export default {
  state,
  getters,
  mutations,
  actions,
};
